-->
<template>
  <div>
    <div class="main scroll-index-1">
      <div class="base--info">
        <!--  -->
        <div class="form-subhead"></div>
        <!--  -->
        <div class="base--info_main">
          <el-form
            ref="formRef"
            :inline="true"
            :model="formData"
            label-position="right"
            label-width="100px"
          >
            <el-form-item label="" prop="name">
              <el-input v-model="formData.title" size="small" />
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="base--info">
        <div class="form-subhead"></div>

        <div class="base--info_main">
          <el-form
            ref="formRef"
            :inline="true"
            :model="formData"
            label-position="right"
            label-width="100px"
          >
            <el-form-item label="" prop="name">
              <div style="border: 1px solid #ccc; margin-top: 20px">
                <!--  -->
                <Toolbar
                  style="border-bottom: 1px solid #ccc"
                  :editor="editor"
                  :defaultConfig="toolbarConfig"
                  :mode="mode"
                />
                <Editor
                  style="height: 500px; overflow-y: hidden"
                  v-model="html"
                  :defaultConfig="editorConfig"
                  :mode="mode"
                  @onCreated="onCreated"
                />
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
    <el-button type="primary" @click="save"> </el-button>
    <el-button type="warning" @click="close()"> </el-button>
  </div>
</template>

<script>
import { richTextDetail } from "@/api/allRequset";
import { Editor, Toolbar } from "@wangeditor/editor-for-vue";

export default {
  name: "richTextExampleDetail",
  props: {
    rowId: {
      type: String,
    },
  },
  components: { Editor, Toolbar },
  data() {
    return {
      showWhatFlag: true, // 
      // 
      formData: {
        postId: "",
        title: "",
        content: "",
      },

      editor: null,
      toolbarConfig: {},
      mode: "default", // or 'simple'
      editorConfig: { placeholder: "" },
      html: "",
    };
  },
  methods: {
    /**
     * @Event 
     * @description: editor
     * */
    onCreated(editor) {
      this.editor = Object.seal(editor); //  Object.seal() 
    },

    /**
     * @Event 
     * @description:  formData  
     * */
    save() {
      console.log(this.formData);
      console.log(this.html);
    },

    /**
     * @Event 
     * @description: 
     * */
    close() {
      this.$parent.showOrHide();
    },

    /**
     * @Interface 
     * @description: 
     * */
    richTextDetail() {
      richTextDetail(this.rowId).then((res) => {
        this.formData = res.data.data;
      });
    },
  },
  created() {},
  mounted() {
    this.richTextDetail();
    /**
     * @Event 
     * @description:  
     * */
    setTimeout(() => {
      if (this.formData.content) {
        this.html = this.formData.content;
      }
    }, 100);
  },
};
</script>

