<template>
  <div class="rich-text">
    <backHomeBtn v-if="show === 0">
      <template>
        <div style="border: 1px solid #ccc; margin-top: 20px">
          <!--  -->
          <Toolbar
            style="border-bottom: 1px solid #ccc"
            :editor="editor"
            :defaultConfig="toolbarConfig"
            :mode="mode"
          />
          <Editor
            style="height: 500px; overflow-y: hidden"
            v-model="html"
            :defaultConfig="editorConfig"
            :mode="mode"
            @onCreated="onCreated"
          />
        </div>

        <el-button style="margin-top: 20px" type="primary" @click="showExample">
          
        </el-button>
      </template>
    </backHomeBtn>

    <richTextExample v-if="show === 1" ref="richTextExample" />
  </div>
</template>

<script>
import Vue from "vue";
import { Editor, Toolbar } from "@wangeditor/editor-for-vue";
import richTextExample from "@/views/richText/richTextExample";

export default Vue.extend({
  components: { Editor, Toolbar, richTextExample },
  data() {
    return {
      editor: null,
      html: "<p>hello</p>",
      toolbarConfig: {},
      editorConfig: { placeholder: "..." },
      mode: "default", // or 'simple'

      show: 0, // 
    };
  },
  methods: {
    onCreated(editor) {
      this.editor = Object.seal(editor); //  Object.seal() 
    },

    /**
     * @Event 
     * @description: 
     * */
    showExample() {
      this.show = 1;
    },

    /**
     * @Event 
     * @description: 
     * */
    close() {
      this.show = 0;
    },
  },
  mounted() {
    /**
     * @Event 
     * @description:   ajax 
     * */
    setTimeout(() => {
      this.html = "<p> Ajax  HTML</p>";
    }, 1500);
  },
  beforeDestroy() {
    const editor = this.editor;
    if (editor == null) return;
    editor.destroy(); // 
  },
});
</script>
<!-- editor -->
