/*                                        */

/*                                        */
export function gaoDeWeather(city) {
  return request({
    url: "https://restapi.amap.com/v3/weather/weatherInfo",
    method: "get",
    params: {
      key: "e3d4cbac03e5c2a465ab04b682ca005f", // 
      city: city, // 
      extensions: null, //  base/allbase:all:
      output: null, //  (JSON,XML)
    },
  });
}
